@import url("https://use.fontawesome.com/releases/v5.0.6/css/all.css");
@import url("https://fonts.googleapis.com/css?family=Open+Sans:300,400,400i,600,700,800");
@import url("https://fonts.googleapis.com/css?family=Open+Sans:300,400,600,700,800");
.template-list {
  list-style: none; }
  .template-list .template-list-item {
    padding: 0.5rem 0; }
    .template-list .template-list-item a {
      text-decoration: none;
      color: black; }
      .template-list .template-list-item a:hover {
        color: #BEA243; }

/*General Styling*/
.container {
  display: block;
  margin-top: 10%; }

h1 {
  font-size: 3em;
  font-weight: 800;
  text-transform: uppercase;
  margin: 0; }

.container > p {
  font-size: 1em;
  text-transform: uppercase;
  margin: 0; }

[href] {
  color: #468966;
  text-decoration: none;
  font-size: inherit; }

hr {
  display: block;
  -webkit-margin-before: 0.5em;
  -webkit-margin-after: 0.5em;
  -webkit-margin-start: auto;
  -webkit-margin-end: auto;
  border-style: inset;
  border-width: 1px;
  width: 80px; }

/*Burger-Menu*/
.menu {
  position: relative;
  width: 30px;
  height: 30px;
  margin: 25px; }

.menu span {
  margin: 0 auto;
  position: relative;
  top: 12px; }

.menu span:before, .menu span:after {
  position: absolute;
  content: ''; }

.menu span, .menu span:before, .menu span:after {
  width: 30px;
  height: 6px;
  background-color: #FFF0A5;
  display: block; }

.menu span:before {
  margin-top: -12px; }

.menu span:after {
  margin-top: 12px; }

/*Fake-Trigger*/
#sized {
  width: 60px;
  height: 50px;
  color: transparent;
  background-color: transparent;
  border: transparent; }

/*Modal-Box*/
.modal {
  display: none;
  position: fixed;
  z-index: 1;
  padding: 0.5%;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto; }

.modal-content {
  position: relative;
  margin: auto;
  padding: 0;
  width: 100%;
  height: 98vh;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  -webkit-animation-name: animatetop;
  -webkit-animation-duration: 0.4s;
  animation-name: animatetop;
  animation-duration: 0.4s;
  background-color: #FFF0A5; }

.modal-header {
  padding: 2px 16px;
  background-color: #FFF0A5;
  border-bottom: none;
  height: 35vh; }

.modal-body {
  padding: 2px 16px;
  background-color: #FFF0A5; }

.modal-footer {
  padding: 2px 16px;
  background-color: #FFF0A5;
  color: black;
  border-top: none;
  text-align: center; }

.modal-footer hr {
  margin-top: 20vh; }

.modal-footer > p {
  color: #468966; }

.modal-footer h3 {
  text-transform: none;
  font-size: 4vh; }

.close {
  color: #468966;
  float: left;
  font-size: 5em;
  font-weight: bold;
  opacity: 1; }

.close:hover,
.close:focus {
  color: #B64926;
  text-decoration: none;
  cursor: pointer; }

@-webkit-keyframes zoomIn {
  from {
    opacity: 0;
    -webkit-transform: scale3d(0.3, 0.3, 0.3);
    transform: scale3d(0.3, 0.3, 0.3); }
  50% {
    opacity: 1; } }

.zoomIn {
  -webkit-animation-name: zoomIn;
  animation-name: zoomIn; }

.animated {
  -webkit-animation-duration: .3s;
  animation-duration: .3s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both; }

/*Navigation Inner Styling*/
.nav ul li a:hover {
  text-decoration: none;
  color: #B64926; }

.nav ul li a {
  padding: 1em;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 3vw;
  letter-spacing: -0.05em; }

.nav ul li {
  display: inline-block;
  font-size: 3em; }

.nav ul {
  clear: both;
  width: 100%;
  text-align: center; }

/*Mobile Optimization*/
/* Portrait Tablet to Landscape and Desktop */
@media (min-width: 768px) and (max-width: 979px) {
  .nav ul li {
    display: block;
    height: 45px; }
  .nav ul {
    clear: both;
    width: 100%;
    text-align: center;
    padding: 0; }
  .nav ul li a {
    padding: 0;
    text-transform: uppercase;
    font-weight: 700;
    font-size: 6vw;
    letter-spacing: -0.05em; } }

/* Landscape-Phone to Portrait Tablet*/
@media (max-width: 767px) {
  .nav ul li {
    display: block;
    height: 38px; }
  .nav ul {
    clear: both;
    width: 100%;
    text-align: center;
    padding: 0; }
  .nav ul li a {
    padding: 0;
    text-transform: uppercase;
    font-weight: 700;
    font-size: 6vw;
    letter-spacing: -0.05em; } }

/*Landscape-Phone Downwards*/
@media (max-width: 480px) {
  .nav ul li {
    display: block;
    height: 38px; }
  .nav ul {
    clear: both;
    width: 100%;
    text-align: center;
    padding: 0; }
  .nav ul li a {
    padding: 0;
    text-transform: uppercase;
    font-weight: 700;
    font-size: 10vw;
    letter-spacing: -0.05em; } }

.dashboard-layout {
  margin-left: 50px; }
  .dashboard-layout ul {
    list-style: none; }
